//
// contribute
//
#contribute {
  @extend %section-top-margin-narrow;
  .wrapper>.section-header {
    padding: rem(30px) 0 rem(100px);
  }
  .chart {
    position: relative;
    background-color: #fff;
    .mix-chart {
      @extend %clearfix;
      background-color: $bg-gray;
      padding: rem(50px) 0;
      width: 65%;
      .chart-inner {
        float: right;
        margin-right: 20%;
        width: rem(558px);
        height: rem(420px);
        .section-header {
          height: rem(80px);
          margin-bottom: rem(50px);
          .heading {
            color: $text-color-gray;
            font-size: rem(30px);
            font-weight: 700;
            margin-bottom: rem(20px);
            text-align: center;
          }
          .info-status li {
            padding-bottom: rem(5px);
            color: $text-color-gray;
            font-size: rem(18px);
            line-height: 1.26;
            font-weight: 300;
            span.kwh1 {
              background: url('data:img/png;base64,') no-repeat 50% 50%/contain;
              width: rem(20px);
              height: rem(20px);
              display: inline-block;
            }
            span.kwh2 {
              background: url('data:img/png;base64,') no-repeat 50% 50%/contain;
              width: rem(23px);
              height: rem(20px);
              display: inline-block;
            }
          }
        }
        .chart-svg {
          background-image: url('data:img/png;base64,');
          background-position: 0% 50%;
          background-repeat: no-repeat;
          position: relative;
          width: rem(619px);
          height: rem(304px);
          svg {
            position: absolute;
            &#chart-bar {
              left: 12%;
              top: 24%;
            }
            &#point-over {
              left: 7%;
              top: -30%;
              opacity: 0;
            }
            &#chart-lines {
              left: 13.5%;
              top: -5.5%;
            }
            &#chart-point {
              left: 13.5%;
              top: -8%;
            }
          }
        }
      }
    }
    .pie-chart {
      position: absolute;
      right: 0px;
      top: rem(80px);
      width: 40%;
      height: rem(520px);
      background: url('../img/contribute/bg-chart-2.jpg') no-repeat 0% 50%/cover;
      .chart-inner {
        left: 20%;
        width: rem(500px);
        height: rem(450px);
        position: relative;
        .section-header {
          padding: rem(50px) 0 rem(20px);
          .heading {
            color: $text-color-white;
            font-size: rem(30px);
            font-weight: 700;
            margin-bottom: rem(20px);
            text-align: center;
          }
        }
        .pie-svg {
          position: relative;
          width: rem(340px);
          height: rem(358px);
          svg {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
          }
        }
        #textDesc {
          margin:rem(10px) 0 0 rem(-70px);
        }
        .textDesc {
          position: absolute;
          font-size: rem(23px);
          color: $text-color-white;
          line-height: 1.26;
          text-align: center;
          .numTxt {
            font-size: rem(26px);
            color: $text-color-white;
          }
          &.textDesc1 {
            top: 50%;
            left: 50%;
            margin: rem(42px) 0 0 rem(-40px);
            color: $text-color-gray;
            .numTxt {
              color: $text-color-orange;
            }
          }
          &.textDesc2 {
            top: 50%;
            left: 50%;
            margin: rem(-10px) 0 0 rem(50px);
          }
          &.textDesc3 {
            top: 50%;
            left: 50%;
            margin: rem(140px) 0 0 rem(-5px);
          }
          &.textDesc4 {
            top: 50%;
            left: 50%;
            margin: rem(100px) 0 0 rem(-120px);
          }
          &.textDesc5 {
            top: 50%;
            left: 50%;
            margin: rem(5px) 0 0 rem(-140px);
          }
          &.textDesc6 {
            top: 50%;
            left: 50%;
            margin: rem(-80px) 0 0 rem(-220px);
          }
          &.textDesc7 {
            top: 50%;
            left: 50%;
            margin: rem(-120px) 0 0 rem(-200px);
          }
        }
      }
    }
    p.text {
      padding: rem(100px) 0;
    }
  }
  .info-graphic {
    background-color: $bg-gray;
    padding: rem(30px) 0 rem(100px);
    .icon {
      display: inline-block;
      svg {
        overflow: visible;
      }
      &.icon-tree svg{
      	position:absolute;
      }
      &.icon-space svg {
      	position: absolute;
      }
    }
    .sub-header {
      display: inline-block;
      margin:rem(30px) 0 0 rem(250px);
      .title {
        font-size: rem(25px);
        font-weight: 600;
        color: $text-color-gray;
      }
      .text {
        text-align: left;
        color: $text-color-gray;
      }
    }
  }
}